// @flow
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Row, Col, Card, Button } from 'react-bootstrap';
import classNames from 'classnames';

// components
import PageTitle from '../../../components/PageTitle';
import Table from '../../../components/Table';

// dummy data
import { estimates } from './data';

/* estimate column render */
const EstimateColumn = ({ row }) => {
    return (
        <>
            <Link to="#" className="text-body fw-bold">
                #BM{row.original.estimate_id}
            </Link>
        </>
    );
};

/* estimate date column render */
const EstimateDateColumn = ({ row }) => {
    return (
        <>
            {row.original.estimate_date} <small className="text-muted">{row.original.estimate_time}</small>
        </>
    );
};

/* estimate column render */
const StatusColumn = ({ row }) => {
    return (
        <>
            <h5>
                <span
                    className={classNames('badge', {
                        'badge-success-lighten': row.original.estimate_status === 'In Review',
                        'badge-info-lighten': row.original.estimate_status === 'Approved',
                        'badge-warning-lighten': row.original.estimate_status === 'Pending Approval',
                    })}>
                    {row.original.estimate_status === 'In Review' && <i className="mdi mdi-coin me-1"></i>}
                    {row.original.estimate_status === 'Approved' && <i className="mdi mdi-cash me-1"></i>}
                    {row.original.estimate_status === 'Pending Approval' && (
                        <i className="mdi mdi-timer-sand me-1"></i>
                    )}
                    {row.original.estimate_status}
                </span>
            </h5>
        </>
    );
};

/* action column render */
const ActionColumn = ({ row }) => {
    return (
        <>
            <Link to="#" className="action-icon">
                {' '}
                <i className="mdi mdi-eye"></i>
            </Link>
            <Link to="#" className="action-icon">
                {' '}
                <i className="mdi mdi-square-edit-outline"></i>
            </Link>
            <Link to="#" className="action-icon">
                {' '}
                <i className="mdi mdi-delete"></i>
            </Link>
        </>
    );
};

// get all columns
const columns = [
    {
        Header: 'Estimate ID',
        accessor: 'estimate_id',
        sort: true,
        Cell: EstimateColumn,
    },
    {
        Header: 'Date',
        accessor: 'estimate_date',
        sort: false,
        Cell: EstimateDateColumn,
    },
    {
        Header: 'Total',
        accessor: 'total',
        sort: false,
    },
    {
        Header: 'Client Name',
        accessor: 'client_name',
        sort: false,
    },
    {
        Header: 'Status',
        accessor: 'estimate_status',
        sort: false,
        Cell: StatusColumn,
    },
    {
        Header: 'Action',
        accessor: 'action',
        sort: false,
        classes: 'table-action',
        Cell: ActionColumn,
    },
];

// get pagelist to display
const sizePerPageList = [
    {
        text: '10',
        value: 10,
    },
    {
        text: '20',
        value: 20,
    },
    {
        text: '50',
        value: 50,
    },
];

// main component
const Estimates = (): React$Element<React$FragmentType> => {
    const [estimateList, setEstimateList] = useState(estimates);

    // change estimate status group
    const changeEstimateStatusGroup = (EstimateStatusGroup) => {
        let updatedData = estimates;
        //  filter
        updatedData =
            EstimateStatusGroup === 'All'
                ? estimates
                : [...estimates].filter((o) => o.estimate_status?.includes(EstimateStatusGroup));
        setEstimateList(updatedData);
    };

    return (
        <>
            <PageTitle
                title={'Estimates'}
            />

            <Row>
                <Col xs={12}>
                    <Card>
                        <Card.Body>
                            <Row className="mb-2">
                                <Col xl={8}>
                                    <form className="row gy-2 gx-2 align-items-center justify-content-xl-start justify-content-between">
                                        <div className="col-auto">
                                            <div className="d-flex align-items-center w-auto">
                                                <label htmlFor="status-select" className="me-2">
                                                    Status
                                                </label>
                                                <select
                                                    className="form-select"
                                                    id="status-select"
                                                    onChange={(e) => changeEstimateStatusGroup(e.target.value)}>
                                                    <option value="All">All</option>
                                                    <option value="In Review">In Review</option>
                                                    <option value="Pending Approval">Pending Approval</option>
                                                    <option value="Approved">Approved</option>
                                                </select>
                                            </div>
                                        </div>
                                    </form>
                                </Col>

                                <Col xl={4}>
                                    <div className="text-lg-end mt-xl-0 mt-2">
                                        <Button variant="danger" className="mb-2 me-2">
                                            <i className="mdi mdi-basket me-1"></i> Create New
                                        </Button>
                                        <Button variant="light" className="mb-2">
                                            Export
                                        </Button>
                                    </div>
                                </Col>
                            </Row>

                            <Table
                                columns={columns}
                                data={estimateList}
                                pageSize={10}
                                sizePerPageList={sizePerPageList}
                                isSortable={true}
                                pagination={true}
                                isSelectable={true}
                                isSearchable={true}
                                theadClass="table-light"
                                searchBoxClass="mb-2"
                            />
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </>
    );
};

export default Estimates;